body {
  background: #ddd;
  margin: 0;
  padding: 0;
}
#root{
  display: flex;
  justify-content: center;
  height: 100vh;
  
}

.App {
  text-align: center;
  color: #333;
  border: 1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin: 2em;
  min-width: 50%;
  padding: 0 1em;
  box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.15);
}

.App button{
  margin-left: .5em; padding: 10px 20px;border-radius: 6px;cursor: pointer;
  background: #F0EFE9; border-style: none;transition: all .2s;
  outline: none; border-width: 3px;
}
.App > h2 > button{margin: 1em;}

.App > .inputWrapper{ display: flex; justify-content: center;align-items: center;margin-bottom: 1em;}

.App > .todoList > li:nth-child(even) {
  background: transparent;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::after{
  box-sizing: border-box;
}
*::before{
  box-sizing: border-box;
}
ul,ol,dl{
  list-style:none;
}
a{
  color: inherit;
}
.TodoInput{
  line-height: 1.8;
  font-size: 16px;
  padding: 0 .2em;
  flex-grow: 1;
}
.TodoItem {
  padding: .5em;
  display: flex;
  align-items: center;
}

.TodoItem > .title {
  flex-grow: 1;
  padding: 0 .5em;
}
.UserDialog-Wrapper{ 
  /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
  /*内容居中*/  display: flex; justify-content: center; align-items: center;
  background: #F0EFE9;
}
.UserDialog{
  border-radius: 4px;
  min-width: 20em;
  background: white;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left:-2em; }
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer; }
.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }

.UserDialog .forgetPassword > h3{ padding: 1em;}
.UserDialog .forgetPassword > form{ padding: 0 1em 1em;}

.UserDialog form>.row{ display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; text-align: right;}
.UserDialog form>.row.actions{ padding-left: 4em; display: flex; align-items: center; }
.UserDialog form>.row.actions button{ margin-left: .5em; padding: 10px 20px;border-radius: 6px;cursor: pointer;
  background: #F0EFE9; border-style: none;transition: all .2s;
  outline: none; }
  .UserDialog form>.row.actions button:hover{ box-shadow: 0 2px 2px 2px rgba(158, 160, 166, 0.5),0 2px 1px rgba(158, 160, 166, 0.7);;}

.UserDialog button{font: inherit; padding: .2em;}
